h1 {
  text-align: center;
}

.racesTable {
  margin: 32px auto;
  border: 1px solid black;
  border-collapse: collapse;
}

.racesTable > thead > tr > th,
.racesTable > tbody > tr > td {
  border: 1px solid black;
  padding: 4px;
  text-align: center;
  font-size: 20px;
}

.racesTable > thead > tr > th,
.racesTable > tbody > tr:nth-child(even) {
  background-color: rgb(221, 221, 221);
}

.choose-group-container {
  text-align: center;
}

.choose-group-container > h1 {
  font-size: 24px;
  margin: 8px 0;
}

.choose-group-container > button {
  margin: 8px 16px;
  padding: 8px 16px;
  background-color: rgb(255, 230, 92);
  color: inherit;
  border: none;
  font: 18px, inherit;
  cursor: pointer;
  outline: inherit;
}

.choose-group-container > button:hover {
  padding: 6px 14px;
  background-color: rgb(255, 242, 172);
  border: 2px solid rgb(255, 242, 166);
}

.choose-group-container > button:active {
  transform: translateY(2px);
  filter: saturate(150%);
}

.start-race-container {
  text-align: center;
}
